import _get from 'lodash/get';
import './Styles.css';
import {
  SURVEYJS_PROPERTY,
  SURVEYJS_SECTION,
} from 'components/FormBuilder/Constants';

export const Panel = options => {
  const parentNode = _get(options, [SURVEYJS_SECTION.panel, 'parent'], {
    isPanel: false,
  });
  const cssType = _get(
    options,
    [SURVEYJS_SECTION.panel, SURVEYJS_PROPERTY.panelCssType],
    'default',
  );
  const cssClassesPanel = _get(options, ['cssClasses', SURVEYJS_SECTION.panel]);
  if (parentNode.isPanel) {
    cssClassesPanel.title += ' sv-panel__lv2';
  }
  if (cssType === 'borderaround') {
    cssClassesPanel.title += ' sv-panel__lv2 with-border';
    cssClassesPanel.container += ' sv-panel__border-around';
  }
};
