import * as Survey from 'survey-react';
import { Widgets } from './Widgets';
import {
  SURVEYJS,
  SURVEYJS_CATEGORY,
  SURVEYJS_PROPERTY,
  SURVEYJS_SECTION,
  PROPERTY_TYPE,
  ELEMENTS_TYPE,
} from './Constants';
import { nouislider } from 'surveyjs-widgets';
import 'nouislider/distribute/nouislider.min.css';

import {
  rounder,
  getTitleFee,
  getMonetaryTotalAmount,
  triggerMonetaryData,
} from './Expression';

Survey.FunctionFactory.Instance.register('getTitleFee', getTitleFee);
Survey.FunctionFactory.Instance.register('rounder', rounder);
Survey.FunctionFactory.Instance.register(
  'getMonetaryTotalAmount',
  getMonetaryTotalAmount,
);
Survey.FunctionFactory.Instance.register(
  'triggerMonetaryData',
  triggerMonetaryData,
);

Survey.StylesManager.applyTheme(SURVEYJS.theme);
Survey.defaultStandardCss.radiogroup.itemChecked = 'sv-radio-checked';
[Widgets, nouislider].map(control => control(Survey));

Survey.Serializer.addProperty(SURVEYJS_SECTION.survey, {
  name: `${SURVEYJS_PROPERTY.action}:${PROPERTY_TYPE.string}`,
  category: SURVEYJS_CATEGORY.GENERAL,
});

Survey.Serializer.addProperty(SURVEYJS_SECTION.question, {
  name: `${SURVEYJS_PROPERTY.questionId}:${PROPERTY_TYPE.string}`,
  category: SURVEYJS_CATEGORY.GENERAL,
});

Survey.Serializer.addProperty(SURVEYJS_SECTION.panel, {
  name: `${SURVEYJS_PROPERTY.panelCssType}:${PROPERTY_TYPE.string}`,
  category: SURVEYJS_CATEGORY.GENERAL,
});

Survey.Serializer.addProperty(ELEMENTS_TYPE.expression, {
  name: `${SURVEYJS_PROPERTY.expressionCssType}:${PROPERTY_TYPE.string}`,
  category: SURVEYJS_CATEGORY.GENERAL,
});
