import _isObject from 'lodash/isObject';
import _get from 'lodash/get';
import './Styles.css';
import { ELEMENTS_TYPE, SURVEYJS_COMMON_CSS_CLASSES } from '../../Constants';

const Textarea = (survey, options) => {
  const TYPE = ELEMENTS_TYPE.comment;
  const question = _get(options, 'question');
  if (_isObject(question) && question.getType() === TYPE) {
    let cssClasses = _get(options, 'cssClasses');
    if (cssClasses) {
      cssClasses.mainRoot = SURVEYJS_COMMON_CSS_CLASSES.mainRoot;
      cssClasses.title = SURVEYJS_COMMON_CSS_CLASSES.title;
      cssClasses.content = 'sv-question-comment__content';
    }
  }
};

export { Textarea };
