import React from 'react';
import * as Survey from 'survey-react';
import _isEmpty from 'lodash/isEmpty';
import _isObject from 'lodash/isObject';
import {
  ELEMENTS_TYPE,
  SURVEYJS_COMMON_CSS_CLASSES,
} from 'components/FormBuilder/Constants';
import './Styles.css';

export class CustomQuestionFile extends Survey.SurveyQuestionFile {
  // eslint-disable-next-line no-useless-constructor
  constructor(props) {
    super(props);
  }
  get question() {
    this.props.question.allowImagesPreview = false;
    this.props.question.storeDataAsText = false;
    this.props.question.loadFiles = function (files) {
      let _this = this;

      if (_isEmpty(this.survey) || !this.allFilesOk(files)) {
        return;
      }

      this.errors = [];
      this.stateChanged('loading');
      const uploadFileSuccess = (status, data) => {
        if (status === 'error') {
          _this.stateChanged('error');
        }
        if (status === 'success') {
          _this.value = (_this.value || []).concat(
            data.map(function (r) {
              return {
                action: r.action,
                file: r.file,
                id: r.id,
                name: r.file.name,
                type: r.file.type,
                size: r.file.size,
              };
            }),
          );
        }
      };
      const loadFilesProc = () => {
        if (_this.storeDataAsText) return;
        _this.survey.uploadFiles(_this, _this.name, files, uploadFileSuccess);
      };

      if (this.allowMultiple) {
        loadFilesProc();
      } else {
        this.clear(loadFilesProc);
      }
    };
    return this.props.question;
  }
  renderClearButton(className) {
    return !this.question.isEmpty() && !this.isDisplayMode ? (
      <button type="button" onClick={this.handleOnClean} className={className}>
        Clear
      </button>
    ) : null;
  }
  renderPreview() {
    if (!this.question.previewValue) return null;
    const previews = this.question.previewValue.map((val, index) => {
      if (!val) return null;
      const fileSign = (
        <span style={{ width: this.question.imageWidth + 'px' }}>
          {val.name}
        </span>
      );
      return (
        <span
          key={this.question.inputId + '_' + index}
          className={this.question.cssClasses.preview}
        >
          {val.name ? (
            <div className={this.question.cssClasses.fileSign}>{fileSign}</div>
          ) : null}
          {this.question.canPreviewImage(val) ? (
            <img
              src={val.content}
              height={this.question.imageHeight}
              width={this.question.imageWidth}
              alt="File preview"
            />
          ) : null}
          {val.name && !this.question.isReadOnly ? (
            <div>
              <span
                className={this.question.cssClasses.removeFile}
                onClick={event => this.handleOnRemoveFile(val)}
              >
                {this.question.removeFileCaption}
              </span>
              <svg
                className={this.question.cssClasses.removeFileSvg}
                onClick={event => this.handleOnRemoveFile(val)}
                viewBox="0 0 16 16"
              >
                <path d="M8,2C4.7,2,2,4.7,2,8s2.7,6,6,6s6-2.7,6-6S11.3,2,8,2z M11,10l-1,1L8,9l-2,2l-1-1l2-2L5,6l1-1l2,2l2-2l1,1L9,8 L11,10z" />
              </svg>
            </div>
          ) : null}
          <div className={this.question.cssClasses.fileSignBottom}>
            {fileSign}
          </div>
        </span>
      );
    });
    return <div>{previews}</div>;
  }
}

Survey.ReactQuestionFactory.Instance.registerQuestion('file', props => {
  return React.createElement(CustomQuestionFile, props);
});

export const FileUpload = (survey, options) => {
  const { question, cssClasses = {} } = options;

  if (_isObject(question) && question.getType() === ELEMENTS_TYPE.file) {
    cssClasses.mainRoot = SURVEYJS_COMMON_CSS_CLASSES.mainRoot;
    cssClasses.title = SURVEYJS_COMMON_CSS_CLASSES.title;
    cssClasses.root += ' sv-file-root';
    cssClasses.chooseFile += ' sv-file-btn sv-btn-primary';
    cssClasses.removeButtonBottom += ' sv-file-btn';
  }
};
