// @flow
import * as React from 'react';
import { Survey, Model } from 'survey-react';
import 'survey-react/modern.css';
import {
  RadioGroup,
  Checkbox,
  DatetimePicker,
  Dropdown,
  SingleInput,
  Textarea,
  MultipleText,
  MatrixSingle,
  MatrixMultiple,
  MatrixDynamic,
  Boolean,
  PhoneInput,
  Panel,
  Expression,
  // eslint-disable-next-line no-unused-vars
  File,
  FileUpload,
  PanelDynamic,
} from './Elements';
import './Styles.css';
import './Elements/Step/Style.css';
import './Elements/Step/Button.css';
import { onValidateHandler } from './Validators';
import _get from 'lodash/get';
import { ELEMENTS_TYPE, SURVEYJS_MODE } from './Constants';
import { SURVEYS_ERROR_MESSAGE } from './Messages';
import _isEmpty from 'lodash/isEmpty';
import _isEqual from 'lodash/isEqual';
import { toastr } from 'react-redux-toastr';
import './setting';
import {
  isFormInDisplayMode,
  applyFormProperty,
  switchIdToName,
} from 'helpers/SurveyJS';

type Props = {|
  formTemplate: Object,
  onCompleteHandler: Function,
  onSaveExitHandler: Function,
  onPageChangedHandler: Function,
  onFilesUploadHandler: Function,
  formData: Object,
  mode: String,
  formProperty: Object,
|};
const surveyModel = (template, mode) => {
  const survey = new Model(template);

  survey.completeText = 'Submit';

  survey.onUpdateQuestionCssClasses.add((survey, options) => {
    [
      RadioGroup,
      Checkbox,
      DatetimePicker,
      Dropdown,
      SingleInput,
      Textarea,
      MultipleText,
      MatrixSingle,
      MatrixMultiple,
      MatrixDynamic,
      Boolean,
      PhoneInput,
      Expression,
      FileUpload,
      PanelDynamic,
    ].map(control => control(survey, options));
  });

  survey.onUpdatePanelCssClasses.add((senders, options) => {
    Panel(options);
  });

  survey.onAfterRenderQuestion.add((sender, options) => {
    const question = _get(options, 'question');
    if (question.getType() === ELEMENTS_TYPE.file) {
      question.storeDataAsText = false;
      question.allowImagesPreview = false;
    }
  });
  if (isFormInDisplayMode(mode)) survey.mode = SURVEYJS_MODE.display;

  return survey;
};

const Engine = (props: Props): React.Element<typeof Survey> => {
  const {
    formTemplate,
    onCompleteHandler,
    onSaveExitHandler,
    onPageChangedHandler,
    formData,
    onFilesUploadHandler,
    mode,
    formProperty,
  } = props;

  const survey = surveyModel(formTemplate, mode);

  const removeSaveExitButton = () => {
    const saveExitButton = document.getElementById('save-exit-btn');
    if (saveExitButton) saveExitButton.remove();
  };

  const addSaveExitButton = () => {
    const id = 'save-exit-btn';
    let saveExitButton = document.getElementById(id);
    if (saveExitButton === null) {
      saveExitButton = document.createElement('input');
      saveExitButton.className = ` sv-btn sv-footer__${id}`;
      saveExitButton.value = 'Save & Exit';
      saveExitButton.type = 'button';
      saveExitButton.id = id;
      saveExitButton.addEventListener('click', () => {
        onSaveExitHandler(survey, formTemplate);
      });
      const [footer] = document.getElementsByClassName('sv-body__footer');
      footer.appendChild(saveExitButton);
    }
  };

  const fetchSavedData = () => {
    const data = switchIdToName(survey, formData, formTemplate);
    survey.data = data;
  };

  survey.onUploadFiles.add((survey, options) => {
    return onFilesUploadHandler({ survey, options });
  });

  survey.onComplete.add(survey => {
    if (!isFormInDisplayMode(mode)) onCompleteHandler(survey, formTemplate);
  });

  survey.onCurrentPageChanged.add((survey, options) => {
    if (_get(options, 'isNextPage') && !isFormInDisplayMode(mode)) {
      onPageChangedHandler(survey, formTemplate);
    }
  });

  const { submitOnConfirmation, confirmationMessage = '' } = formTemplate;
  submitOnConfirmation &&
    confirmationMessage &&
    survey.onCompleting.add(function (sender, options) {
      options.allowComplete = window.confirm(confirmationMessage);
    });

  React.useEffect(() => {
    // filtering to get a list of type file questions
    survey.getAllQuestions().forEach(question => {
      applyFormProperty(question, formProperty);
    });
    addSaveExitButton();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  React.useEffect(() => {
    if (isFormInDisplayMode(mode)) removeSaveExitButton();
  }, [mode]);

  React.useEffect(() => {
    if (_isEmpty(formData)) return;
    try {
      fetchSavedData();
    } catch (e) {
      toastr.error(SURVEYS_ERROR_MESSAGE.COMMON);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [formData]);

  return (
    <>
      <Survey model={survey} onValidateQuestion={onValidateHandler} />
    </>
  );
};

Engine.defaultProps = {
  formTemplate: '',
  onCompleteHandler: () => {},
  onSaveExitHandler: () => {},
  onPageChangedHandler: () => {},
  mode: 'edit',
};

const areEqual = (prevProps, nextProps) => {
  return _isEqual(prevProps.formData, nextProps.formData);
};
export default (React.memo<Props>(
  Engine,
  areEqual,
): React.AbstractComponent<Props>);
