import _get from 'lodash/get';
import './Styles.css';
import { ELEMENTS_TYPE, SURVEYJS_COMMON_CSS_CLASSES } from '../../Constants';

const MatrixSingle = (survey, options) => {
  const TYPE = ELEMENTS_TYPE.matrixSingle;
  if (options.question.getType() === TYPE) {
    let cssClasses = _get(options, 'cssClasses');
    if (cssClasses) {
      cssClasses.mainRoot = SURVEYJS_COMMON_CSS_CLASSES.mainRoot;
      cssClasses.title = SURVEYJS_COMMON_CSS_CLASSES.title;
      cssClasses.headerCell = 'header-cell';
      cssClasses.header = '';
      cssClasses.titleOnAnswer = '';
      cssClasses.itemHover = '';
      cssClasses.content = '';
    }
  }
};

export { MatrixSingle };
