import _get from 'lodash/get';
import './Styles.css';
import {
  ELEMENTS_TYPE,
  SURVEYJS_COMMON_CSS_CLASSES,
} from 'components/FormBuilder/Constants';

const RadioGroup = (survey, options) => {
  const TYPE = ELEMENTS_TYPE.radioGroup;
  if (options.question.getType() === TYPE) {
    let cssClasses = _get(options, 'cssClasses');
    if (cssClasses) {
      cssClasses.mainRoot += ' ' + SURVEYJS_COMMON_CSS_CLASSES.mainRoot;
      cssClasses.title = SURVEYJS_COMMON_CSS_CLASSES.title;
      cssClasses.root += ' sv-radio-group-wrapper';
      cssClasses.column += ' sv-radio-group-item';
      cssClasses.controlLabel = 'sv-radio-label';
      cssClasses.label = 'sv-radio-selectbase__label';
      cssClasses.content = 'sv-question-radio__content';
      cssClasses.titleOnAnswer = '';
      cssClasses.itemHover = '';
      cssClasses.itemInline = '';
      cssClasses.itemControl = '';
    }
  }
};

export { RadioGroup };
